<template>
  <main class="container-fluid">
    <h1>搜尋客戶</h1>
    <form class="form">
      <div class="input-group input-lg">
        <div class="input">
          <input
          type="text"
          name="keyowrd"
          id="keyowrd"
          class="form-control"
          v-model="keyowrd"
          placeholder="輸入姓名、手機號碼或電話">
        </div>
        <button
        class="btn btn-primary btn-icon"
        type="submit"
        @click.stop.prevent="doSearch()">
          <i class="icon-search i-24"></i>
        </button>
      </div>
    </form>

    <h6>最近的搜尋紀錄</h6>
    <div class="recent-list">
      <router-link :to="`/search-result/${item}`" class="btn btn-default"
      v-for="(item, i) in recentSearch"
      :key="`recent-search-item-${i}`">
        {{item}}
      </router-link>
    </div>
  </main>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      keyowrd: '',
      recentSearch: [
        '陳', '0987654321', '歐陽娜娜', 'A135713198', 'A135713198'
      ]
    }
  },
  methods: {
    doSearch () {
      if (this.keyowrd === '') {
        this.$toast.info({title: '請輸入關鍵字'})
        return
      }
      this.$router.push(`/search-result/${this.keyowrd}`)
    }
  },
  mounted () {
  }
}
</script>
