<template>
  <div id="app" :class="`page-${$route.name}`">
    <header v-if="$route.name!=='login'">
      <div id="sidebar" class="sidebar-menu" :class="{'is-open': sidebarOpen}">
        <div class="sidebar-container">
          <header>
            <a class="close" @click="sidebarOpen=false"><i class="icon-close"></i></a>
            <div class="user">
              <span class="avatar"></span>
              <div class="user-content">
                <span class="name">麥寶蜆</span>
                <span class="text">第7工第28天</span>
              </div>
            </div>
          </header>
          <main>
            <div class="list">
              <router-link to="/" class="item">
                <span class="float-right text-gray">❯</span>
                首頁
              </router-link>
              <router-link to="/customer/create" class="item">
                <span class="float-right text-gray">❯</span>
                新增客戶
              </router-link>
              <router-link to="/" class="item">
                <span class="float-right text-gray">❯</span>
                商機專區
              </router-link>
              <router-link to="/" class="item">
                <span class="float-right text-gray">❯</span>
                條件篩選
              </router-link>
            </div>
            <div class="text-right">
              <a href="/crm/index.html?size=desktop" class="btn btn-text text-green text-underline" role="button">
                <i class="icon-back_pc"></i>
                返回電腦版
              </a>
            </div>
          </main>
          <footer>
            <router-link to="/login" class="btn btn-default">登出</router-link>
          </footer>
        </div>
      </div>
      <nav>
        <div class="nav-item left">
          <a v-if="$route.name==='insurance'" @click="$router.back()" class="btn btn-trigger">
            <i class="icon-back"></i>
          </a>
          <a v-else class="btn btn-trigger" @click="sidebarOpen = true">
            <i class="icon-menu"></i>
          </a>
        </div>
        <div class="nav-item center" v-if="$route.name==='index'">
          <div class="nav-logo"></div>
        </div>
        <div class="nav-item center" v-else-if="$route.name==='insurance'">
          <h5 class="text-white m-0">保單明細</h5>
        </div>
        <div class="nav-item center" v-else>
          <div class="input-group">
            <input type="text" v-model="keyword" placeholder="輸入姓名、手機號碼或電話" class="form-control">
            <button class="btn btn-text">
              <i class="icon-search"></i>
            </button>
          </div>
        </div>
        <div class="nav-item right" v-if="$route.name==='insurance'">
        </div>
      </nav>
    </header>
    <router-view/>
    <div id="gotop" @click="goTop()">
      <div class="icon">
        <i class="icon-back"></i>
      </div>
      <div class="text"><small>回頂部</small></div>
    </div>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
  name: 'App',
  computed: {
    ...mapGetters([
      'PageLock'
    ])
  },
  data () {
    return {
      keyword: '',
      sidebarOpen: false
    }
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    ...mapActions([
      'setPageLock'
    ]),
    goTop () {
      this.$('html, body').scrollTop(0)
      // document.scrollTop = 0
      // document.body.scrollTop = 0
      // document.scrollingElement.scrollTop = 0
    },
    fetchData () {
      this.sidebarOpen = false
      this.setPageLock(false)
      setTimeout(() => {
        this.$('html, body').scrollTop(0)
        // document.scrollTop = 0
        // document.body.scrollTop = 0
        // document.scrollingElement.scrollTop = 0
      }, 250)
    }
  },
  mounted () {
    document.body.onscroll = () => {
      let scroll = document.scrollTop || document.body.scrollTop || document.scrollingElement.scrollTop || 0;
      let go = document.querySelector('#gotop')
      if( scroll > 300) {
        go.classList.add('is-show')
      } else {
        go.classList.remove('is-show')
      }
    }
  }
}
</script>

