import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/Index.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/login',
      name: 'login',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Login.vue')
    },
    {
      path: '/search-result/:keyword',
      name: 'search-result',
      component: () => import(/* webpackChunkName: "about" */ './views/SearchResult.vue')
    },
    {
      path: '/customer/create',
      name: 'create-customer',
      component: () => import(/* webpackChunkName: "about" */ './views/CustomerCreate.vue')
    },
    {
      path: '/customer/:id',
      name: 'customer',
      component: () => import(/* webpackChunkName: "about" */ './views/Customer.vue')
    },
    {
      path: '/customer/:id/insurance/:insureID',
      name: 'insurance',
      component: () => import(/* webpackChunkName: "about" */ './views/Insurance.vue')
    }
  ]
})
